import { CoordColorList } from "./coordColorList";
import { ImageCanvas } from "./imageCanvas";
import { MagnisyGlassesImp } from "./magnisyGlasses";
import { ScopeListener } from "./scopeListener";
import { buildCompareFeature, setClipboard, showMessage } from "./utils";


let image = new ImageCanvas();
let glassCanvas = <HTMLCanvasElement> document.getElementById("magnify_glasses");
let glasses = new MagnisyGlassesImp(glassCanvas,15,10);
image.setGlasses(glasses);

let coordColorList = new CoordColorList();
image.setFeatureListener(coordColorList);


let buildFeature = document.getElementById("build_feature");
if(buildFeature){
    buildFeature.onclick = (e)=>{
        let feature = buildCompareFeature(coordColorList.getCoordColors());
        setClipboard(feature).then((e)=>{
            showMessage(`比色特征已经复制到你的剪切板`);
        });
    }
}
let scopeListener = new ScopeListener(true);
image.setScopeListener(scopeListener);




document.onkeydown=(e)=>{
    if(e.code === "ArrowLeft"){
        image.mouseMove(-1,0);
    }else if(e.code === "ArrowRight"){
        image.mouseMove(1,0);
    }else if(e.code === "ArrowUp"){
        image.mouseMove(0,-1);
    }else if(e.code === "ArrowDown"){
        image.mouseMove(0,1);
    }
}
